@tailwind base;
@tailwind components;
@tailwind utilities;

.nav-link {
  margin-right: 10px;
}
.nav-link {
  margin-right: 10px; /* Ajusta el valor según tu preferencia */
}

.active {
  @apply text-black mx-2;
  margin-right: 10px;
}
.active:hover {
  @apply text-blue-500; /* Cambia a tu clase de color azul de Tailwind CSS */
}

.banner{
  background-color: black;
  flex-direction:row;
  position: relative;
  width: 100%;
  height: 100%;

}

.logoNE {
  z-index: 1;
  margin-top: 0;
  margin-bottom: 0%;
  margin-left: 90%;
  box-shadow: #805D93;
  
}

.category_img {
  padding: 20px;
  border-radius: 25%;
  position: relative;
  justify-content: center;
  justify-items: center;
  
}

.container_HeaderIn {
  width: 100%; /* Asegura que el contenedor ocupe el 100% del ancho del contenedor padre */
  max-width: 1200px; /* Limita el ancho máximo para mejorar la legibilidad en pantallas grandes */
  margin: 0 auto; /* Centra el contenedor en el eje horizontal */
  padding: 20px; /* Añade espacio interno para mejorar la legibilidad */

}


.title h1{
padding: 5px;
margin-top: 0;
margin-bottom: 0px;
position: relative;
text-align: right;
margin-right: 10px;
font-size: 18px;
text-transform: uppercase;
color: #FFF;
}

.presenta {
  color: #FFF;

  margin-bottom: 10px;
  line-height: 30px;
  font-size: 2em;
  font-stretch: condensed;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  justify-content: left;
  text-overflow: clip;
  display: block;
  text-align: right;
  margin: 0 0 0 0;
}
