.pagination {
  position: relative;
  margin-top: 10%;
}
.button{
  position: relative;
  display: flex;
  flex-direction: row;
  

  background-color:  #76A8A5;
  color: #FFF;
  padding: 5px;
  border-radius: 10px;
  text-decoration: none;
  font-weight: 600;

}
.button:hover{
background-color: #FAF9C7;
color: black;
}
/* Estilos para el contenedor principal del Navbar
.max-w-7xl {
  display: flex;
  justify-content: space-between;

} */
.previous {
  position: relative;
  right: 10px;
}

/* Estilos para el logo en la esquina izquierda */
.logoend {

  margin-left:0%;
  position: relative;
  left: 100px;
}



/* Estilos para el formulario de búsqueda y otros elementos en el centro */
.nav_in {
  position: relative;
  left: 0%;

  margin-right: auto; /* Alinea elementos desde la derecha */
}

/* Estilos para el menú en la esquina derecha */
.logo_begining {
  width: auto;



}


/* Estilos para los elementos del menú */
.menu {
  
  position: relative;

  left: 100px;
}

/* Estilos para el logo en la esquina derecha en la versión móvil */
.lg:hidden .flex-shrink-0 {
  margin-left: auto;
}
.header {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  align-items: center; /* Centra horizontalmente los elementos */
 /* Ajusta la altura al 100% del viewport (pantalla) */  
}

@media screen and (max-width:600px) {
  .container_HeaderIn {
    font-size: 16px; /* Ajusta el tamaño de la fuente según tus preferencias */
    line-height: 1.5; /* Ajusta el interlineado para mejorar la legibilidad */
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .presenta {
    padding: 5px;
    position: relative;
    left: 0%;
    margin-left: 0%;
    margin-right: 0%;
    text-align: right;
    font-size: 20px;

  }
  h2 {
    padding: 5px;
    position: relative;
    left: 0%;
    margin-left: 0%;
    margin-right: 20%;
    text-align: center;
  }
  .logoNE {
    padding: 3px;
    width: 40%;
    height: 40%;
  }
}
